.side,
.cube {
  width: 182px;
  height: 182px;
  box-sizing: border-box; }

.cube {
  transform-style: preserve-3d;
  animation: rotate 20s infinite linear;
  box-sizing: border-box;
  position: relative; }
  .cube .border {
    left: 0;
    position: absolute;
    width: 176px;
    height: 176px;
    top: 0;
    bottom: 0;
    right: 0;
    box-sizing: border-box;
    border: 4px dotted white; }

.cube:before,
.cube:after {
  content: "";
  display: block;
  position: absolute;
  width: 180px;
  height: 180px;
  border: 4px dotted white; }
  .cube:before:before,
  .cube:after:before {
    content: 'a';
    display: block;
    position: absolute;
    width: 180px;
    height: 180px; }

.cube:before {
  transform: rotateX(90deg); }

.cube:after {
  transform: rotatey(90deg); }

.side {
  position: absolute;
  width: 180px;
  height: 180px;
  border: 4px solid white; }

.back {
  transform: translateZ(-90px);
  background: transparent; }

.left {
  transform: translateX(-90px) rotateY(90deg);
  background: transparent; }

.right {
  transform: translateX(90px) rotateY(90deg);
  background: transparent; }

.top {
  transform: translateY(-90px) rotateX(90deg);
  background: transparent; }

.bottom {
  transform: translateY(90px) rotateX(90deg);
  background: transparent; }

.front {
  transform: translateZ(90px);
  background: transparent; }

@keyframes rotate {
  100% {
    transform: rotatex(360deg) rotateY(360deg) rotateZ(360deg); } }

.right_cont {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40%;
  -webkit-perspective: 1000px;
  perspective: 1000px;
  -webkit-perspective-origin: center -90px;
  perspective-origin: center -95px; }

